import React from "react"
import { useStaticQuery, graphql } from "gatsby"

const LastUpdated = () => {
    const data = useStaticQuery(graphql`
    query {
      file(sourceInstanceName: { eq: "pages" }, relativePath: { eq: "index.mdx" }) {
        modifiedTime(formatString: "MMMM D, YYYY")
      }
    }
  `)

    return (
        <div style={{ fontSize: `0.8rem`, marginTop: `2rem`, color: `#666` }}>
            Last updated: {data.file?.modifiedTime}
        </div>
    )
}

export default LastUpdated
